<template>
  <div class="showManageContent">

  	<selectObject></selectObject>

  	<div class="showManageTitle">
  	  展示管理
  	</div>
  	<div class="showManageDetail">
  	  <div class="dleft">
  	  	<p>一级模块</p>
  	  	<div class="dleftContent">
  	  	  <div class="show">
            <div class="showCheck"><span></span></div>
            <em>成果展示</em>
  	  	  </div>

  	  	</div>
  	  </div>
  	  <div class="dRight">
        <div class="dRightHeader">
          <div class="localtion">
            二级模块位置：
          </div>
          <div class="choose">
          	<span 
             :class="{active : cur === index}" 
              v-for="(item, index) in secondArr"
              @click="selectTab(index)"
            >
              {{item.name}}
            </span>
          </div>
        </div>

        <component :is="currentView" :msg="msg"></component>
  	  </div>
  	</div>
  </div>
</template>
<style lang="less" scoped>
  .showManageContent{
  	margin:60px 0 60px 260px;
  	.showManageTitle{
      color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  text-align: left;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
  	}
  	.showManageDetail{
  	  padding: 20px 50px; 
  	  display: flex;
  	  .dleft{
  	  	box-shadow: 0 0 2px #eee;
  	  	height: 260px;
  	  	p{
  	  	  margin-top: 0;
  	  	}
  	  	width: 160px;
  	  	padding: 10px;
  	  	background: rgb(236,236,236);
  	  	.dleftContent{
  	  	  height: 200px;
  	      background: white;
  	      padding: 12px;
  	  	}
  	  	.show{
	  	  	display: flex;
	  	  	font-size: 12px;
	  	  	em{
	  	  		font-style:normal;
	  	  		margin-left: 10px;
	  	  	}
	  	  	.showCheck{
            padding: 2px;
            border:1px solid #ccc;
            span{
            	display: block;
            	width: 10px;
            	height: 10px;
            	background: rgb(76,180,231);
            }
	  	  	}
	  	  }
  	  }
  	  .dRight{
  	  	margin-left: 20px;
  	  	flex: 1;
  	  	.dRightHeader{
  	  		display: flex;
  	  		padding: 16px 20px;
  	  		background: #ececec;
  	  		box-shadow: 0 0 2px #eee;
  	  		.choose{
  	  			span{
  	  				padding: 5px;
  	  				margin-left: 20px;
              cursor: pointer;
  	  			}
  	  			.active{
  	  				background: #4cb4e7;
  	  				color: white;
  	  			}
  	  		}
  	  	}
  	  }
  	}
  }
</style>
<script>
  import selectObject from "./selectObject";
  import tab from "./manageContent/manageContent";
  
	export default {
    data() {
      return {
        secondArr: [{
          name   : "成果首页"
        },{
          name   : "成果类别"
        }],

        cur: 0,
        currentView: 'tab',
        msg: ''
      }
    },
    methods: {
      selectTab(index){
        this.cur = index;
        this.msg = index;
      }
    },
    components: {
    	selectObject,
      tab
    }
  }
</script>


