<template>
	<div class="dRightContent">
		<div class="dRightDetail">
			<p>位置一</p>
	    <div class="dRightDetail_1">
	      <div class="mark">
	      	注：首页轮播页，可通过“+”添加需要展示的轮播页数量
	      </div>
	      <div class="list">
	      	<ul class="clearfix">
	      		<li v-for="(item, index) in blogOneArr" :class="{isDelete: item.isDelete}" v-if="!item.isDelete" >
	      			<div class="listNum">
	              <div class="num">{{index + 1}}</div>
	              <p></p>
	              <div class="listMark">
	              	轮播图
	              </div>
	      			</div>
	      			<div class="listText" >
                <div class="blogDelete" v-if="item.isDelete">
                  已删除
                </div>
	      				<div class="listTextC" v-if="item.blogTitle">
                  <div class="listTextL">
                    <div class="listTitle">
                    	成果名称
                    </div>
                    <div class="listName">
                    	{{item.blogTitle}}
                    </div>
                    <div class="report">
                    	发布人
                    </div>
                    <div class="reportor">
               				{{item.userNickname || "匿名"}}
                    </div>
                    <div class="date">
                    	{{formatDate(item.createTime)}}
                    </div>
                  </div>

                  <div class="listTextR" @click="deleteItem(index, item.beginBlogId, 1)">
										<span></span>
                  </div>
	              </div>
	              <div class="add" @click="addMask(1, '10'+(index+1)+'')" v-if="!item.blogTitle">
	                <p><img src="../../../assets/images/add.png"></p>
	                <span>选择对象</span>
	              </div>
                <div class="closeBtn" v-if="!item.blogTitle">
                  <i class="el-icon-close addClose" @click.stop="addRemove(1,index)"></i>
                </div>
	          	</div>
	      		</li>
	          <div class="addItem" @click="addItem(1)" v-if="addOne">
	            <img src="../../../assets/images/addItem.png">
	            <p>增加轮播图</p>
	          </div>
	      	</ul>
	      </div>
	    </div>
		</div>
	  
	  <div class="dRightDetail" v-if="!msg">
			<p>位置二</p>
	    <div class="dRightDetail_1">
	      <div class="mark">
	      	注：该位置固定展示视频，只能选择一个有视频的成果
	      </div>
	      <div class="list">
	      	<ul class="clearfix">
	      		<li v-for="(item, index) in blogTwoArr"  v-if="!item.isDelete">
	      			<div class="listNum videoImg">
	              <div class="num">
	              	<img src="../../../assets/images/video.png">
	              </div>
	              <p></p>
	              <div class="listMark">
	              	视频
	              </div>
	      			</div>
	      			<div class="listText">
                <div class="blogDelete" v-if="item.isDelete">
                  已删除
                </div>
	      				<div class="listTextC" v-if="item.blogTitle">
                  <div class="listTextL" >
                    <div class="listTitle">
                    	成果名称
                    </div>
                    <div class="listName">
                    	{{item.blogTitle}}
                    </div>
                    <div class="report">
                    	发布人
                    </div>
                    <div class="reportor">
               				{{item.userNickname || "匿名"}}
                    </div>
                    <div class="date">
                    	{{formatDate(item.createTime)}}
                    </div>
                  </div>

                  <div class="listTextR"  @click="deleteItem(index, item.beginBlogId, 2)">
										<span></span>
                  </div>
	              </div>

                <div class="add" @click="addMask(2, '201')" v-if="!item.blogTitle">
                  <p><img src="../../../assets/images/add.png"></p>
                  <span>选择对象</span>
                </div>
                <div class="closeBtn"  v-if="!item.blogTitle">
                  <i class="el-icon-close addClose" @click.stop="addRemove(2,index)"></i>
                </div>
	          	</div>
	      		</li>
            <div class="addItem" @click="addItem(2)" v-if="addTwo">
              <img src="../../../assets/images/addItem.png">
              <p>增加视频展示</p>
            </div>
	      	</ul>
	      </div>
	    </div>
		</div>

		<div class="dRightDetail" v-if="!msg">
			<p>位置三</p>
	    <div class="dRightDetail_1">
	      <div class="mark">
	      	注：从左到右依次固定展示
	      </div>
	      <div class="list">
	      	<ul class="clearfix">
	      		<li v-for="(item, index) in blogThreeArr"  v-if="!item.isDelete">
              <div class="listNum">
                <div class="num">{{index + 1}}</div>
                <p></p>
                <div class="listMark">
                  轮播图
                </div>
              </div>
              <div class="listText">
                <div class="blogDelete" v-if="item.isDelete">
                  已删除
                </div>
                <div class="listTextC" v-if="item.blogTitle">
                  <div class="listTextL">
                    <div class="listTitle">
                      成果名称
                    </div>
                    <div class="listName">
                      {{item.blogTitle}}
                    </div>
                    <div class="report">
                      发布人
                    </div>
                    <div class="reportor">
                      {{item.userNickname || "匿名"}}
                    </div>
                    <div class="date">
                      {{formatDate(item.createTime)}}
                    </div>
                  </div>

                  <div class="listTextR" @click="deleteItem(index, item.beginBlogId, 3)">
                    <span></span>
                  </div>
                </div>
                <div class="add" @click="addMask(3, '30'+(index+1)+'')" v-if="!item.blogTitle">
                  <p><img src="../../../assets/images/add.png"></p>
                  <span>选择对象</span>
                </div>
                 <div class="closeBtn"  v-if="!item.blogTitle">
                  <i class="el-icon-close addClose" @click.stop="addRemove(3,index)"></i>
                </div>
              </div>
            </li>
            <div class="addItem" @click="addItem(3)" v-if="addThree">
              <img src="../../../assets/images/addItem.png">
              <p class="addThree">增加展示</p>
            </div>
	      	</ul>
	      </div>
	    </div>
		</div>
   <!--成果类别的选择图片弹窗-->
		<selectImg :selectImgMask="selectImgMask" @hideMask="hideMask"></selectImg>
	</div>	
</template>
<style lang="less" scoped>
.dRightContent{
	.dRightDetail{
		border:1px solid #eee;
		margin-top: 20px;
		box-shadow: 0 0 2px #eee;
		>p{
			padding: 10px 20px;
			background: #ececec;
			font-size: 12px;
			text-align: left;
			margin: 0;
		}
	}

	.dRightDetail_1{
		padding: 10px 20px;
    font-size: 12px;
    text-align: left;
    color: #868686;
    .list{
    	ul{
    		margin-left: 0;
    		padding-left: 0;
    		li{
    			list-style: none;
    			margin-left: 0;
    			width: 330px;
    			display: flex;
    			height: 150px;
    			box-shadow: 0 0 2px #ccc;
    			margin-top: 20px;
    			float: left;
    			margin-right: 40px;
    			.listNum{
    				width: 100px;
    				text-align: center;
    				.num{
    					width: 30px;
    					height: 30px;
    					margin: 0px auto;
    					margin-top: 40px;
    					border-radius: 100%;
    					border:2px solid #4cb4e7;
    					text-align: center;
    					line-height: 30px;
    					font-size: 16px;
    					color: #4cb4e7;
    					font-weight: bold;
    				}
    				p{
  						width: 24px;
  						height: 2px;
  						background: #4cb4e7;
  						box-shadow: 0 0 2px #4cb4e7;
  						margin:4px auto;
  					}
  					.listMark{
  						margin-top: 10px;
  						font-size: 12px;
  					}
    			}
    			.videoImg{
    				.num{
    					border:none;
    					img{
    						width: 30px;
    					}
    				}
    			}
    			.listText{
    				flex: 1;
    				cursor: pointer;
    				background: rgb(232,232,231);
    				padding: 10px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            .blogDelete{
              background: red;
              position: absolute;
              right: 0;
              top: 0;
              padding: 2px 6px;
              color: white;
              text-align: center;
              font-size: 12px;
            }
    				.listTextC{
    					display: flex;
    					.listName{
    						font-size: 14px;
    						font-weight: bold;
    						margin: 4px 0 10px 0;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
    					}
    					.reportor{
    						font-size: 14px;
    						font-weight: bold;
    						margin: 4px 0 10px 0;
    					}
    					.date{
    						margin-top: 20px;
    					}
    				}
            .listTextL{
              width: 126px;
              margin-top: 7px;
            }
    				.listTextR{
    					width: 20px;
    					height: 20px;
    					border:3px solid #b5b5b5;
    					border-radius: 100%;
    					text-align: center;
    					cursor: pointer;
    					margin-top:50px;
    					margin-left: 50px;
    					span{
    						display: block;
    						width: 12px;
    						height: 3px;
    						background: #b5b5b5;
    						margin:9px auto;
    					}
    				}
    				.add{
    					text-align: center;
    					>p{
    						font-size: 30px;
    						font-weight: bold;
    						margin-bottom: 10px;
    					}
    					img{
    						width: 20px;
    					}
            }
            .closeBtn{
              position: absolute;
              right:0px;
              top: 0px;
              text-align: right;
              font-size: 20px;
              z-index: 2;
              font-weight: bold;
              width: 30px;
              height: 30px;
            }
    			}
    		}
        .addItem{
          float: left;
          margin-left: 40px;
          cursor: pointer;
          p{
            padding-left: 10px;
          }
          img{
            margin:40px 0 0 0;
          }
          .addThree{
            text-align: center;
          }
        }
    	}
    }
	}
}
</style>
<script type="text/javascript">
  import selectImg from "./selectImg";
  import {mapGetters} from 'vuex';

	export default {
    data() {
    	return {
        selectImgMask: false,
        blogOneArr : [],
        blogTwoArr : [],
        blogThreeArr : [],
        addOne: true,  //位置一的 “增加轮播图” 的状态
        addTwo: true,  //位置二的 “增加视频展示” 的状态
    	  addThree: true //位置三的 “增加展示” 的状态
      }
    },
    props: ['msg'],
    computed:{
      ...mapGetters({
        refreshDataLocal: 'refreshDataLocal',
      })
    },
    watch:{
      refreshDataLocal(val){
        let num = val.isAchievementIndex ? 1 : 2;
        this.getBlog(val.refreshDataLocal, num);
      },
      msg(val) {
        if(val == 0){
          this.getBlog(1, 1);
          this.getBlog(2, 1);
          this.getBlog(3, 1);
        }else{
          //tab上的成果类别
          this.getTabCategory(1, 2);
        }
      }
    },
    methods: {
      addRemove(num, index) {
        if(num == 1){
          this.blogOneArr.splice(index, 1);
        }else if(num == 2){
          this.blogTwoArr.splice(index, 1);
          this.addTwoShow();
        }else{
          this.blogThreeArr.splice(index, 1);
          this.addThreeShow();
        }     
      },
      getTabCategory(num, local) {
        this.blogOneArr = [];
        this.getBlog(num, local);
      },
    	addMask(num, showOrder) {
    		if(!this.msg){
    			this.$store.commit("ADDMASK", {
            mask: true,
            clickLocal: num,
            showOrder: showOrder,
            time: new Date().getTime()
          });
    		}else{
          this.selectImgMask = true;
    		}     
      },
      addItem(num) {   
        let obj = {
          blogTitle: "",
          userId: "",
          createTime: "",
          beginBlogId: ""
        };

        if(num == 1){
          this.blogOneArr.push(obj);
          this.addOneShow();
        }else if(num == 2){
          this.blogTwoArr.push(obj);
          this.addTwoShow(); 
        }else{
          this.blogThreeArr.push(obj);
          this.addThreeShow(); 
        }
      },
      addOneShow() {
        this.addOne = this.blogOneArr.length >= 6 ? false : true;
      },
      addTwoShow() {
        this.addTwo = this.blogTwoArr.length >= 1 ? false : true;
      },
      addThreeShow() {
        this.addThree = this.blogThreeArr.length >= 3 ? false : true;
      },
      addVideo() {
        this.addMask(2, 201);
      },
      deleteItem(index, beginBlogId, num) {
        //num == 1 代表的是位置一的图片轮播 
        //num == 2 代表的是位置二的视频
        //num == 3 代表的是位置三的展示
        
        let params = {
          beginBlogId : beginBlogId
        }
        
        this.$post(`${this.url}/manage/show/configRemove`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            switch(num){
              case 1: 
                this.blogOneArr.splice(index, 1);
                this.addOneShow();
                break;
              case 2: 
                this.blogTwoArr.splice(index, 1);
                this.addTwoShow();
                break
              case 3:
                this.blogThreeArr.splice(index, 1);
                this.addThreeShow();
                break;
            }

            this.$message({
              message: '删除成功',
              type: 'success'
            });
          }
        })
      },
      hideMask() {
        this.selectImgMask = false;
      },
      getBlog(num, local) {
        //local 为1 代表的是成果首页，2为成果类别
        let params = {
          modularType1: 1,
          modularType2: local,
          showType: num
        }
        
        this.$post(`${this.url}/manage/show/getBlogConfig`,
          params
        )
        .then((response) => {
          if(response.code == 200){
            let data = response.data;
            let obj = [];
            for(let i = 0 , len = data.length ; i < len ; i++){
              if(data[i].blogModel!=null){
                obj.push({
                  blogTitle: data[i].blogModel.blogTitle,
                  userId: data[i].blogModel.userId,
                  createTime: data[i].blogModel.createTime,
                  beginBlogId: data[i].beginBlogModel.beginBlogId,
                  userNickname: data[i].blogModel.userNickname,
                  isDelete: data[i].blogModel.isDelete
                })
              }
            } 

            if(num == 1){
              this.blogOneArr = obj;
              this.addOneShow();
            }else if(num == 2) {
              this.blogTwoArr = obj;
              this.addTwoShow();
            }else{
              this.blogThreeArr = obj;
              this.addThreeShow();
            }
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      }
    },

    created() {
      //1 为一级模块 ( 1成果展示 ) ，2 为二级模块 ( 1成果首页 2成果类别 )，三为展示类型( 1轮播页 2视频页 3固定展示页)
      
      this.getBlog(1, 1);
      this.getBlog(2, 1);
      this.getBlog(3, 1);
    },
    components: {
    	selectImg
    }
	}
</script>


