<template>
  <div class="selectMask" v-show="addMask.mask">
		<div class="selectObject">
      <p>
      	<span class="selectTitle">选择对象</span>
        <i class="el-icon-close" @click="close()"></i>
      </p>

			<div class="objectContent">
				<div class="objectLeft">
					<p>选择类别</p>
					<div class="firstClass">
						<category></category>
					</div>
				</div>
				<div class="objectCenter">
					<div class="inputSearch">
						<input type="text" placeholder="请输入搜索内容" v-model="inputSearch">
						<i class="el-icon-search" @click="resultSearch"></i>
          </div>
					<ul class="result" @scroll="handleScroll" ref="getHeight">
            <li v-if="noData">暂无数据</li>
						<el-radio-group 
              v-model="radio"
              @change="selectedVal"
            >
					    <el-radio :label="item" v-for="(item, index) in detailArr" :key="index">
               {{item.blogTitle}}
              </el-radio>
					  </el-radio-group>
					</ul>
				</div>
        <div class="objectRight">
          <p>已选对象预览</p>
          <div class="objectRightContent" v-if="selectedMsg.blogTitle">
            <p>{{selectedMsg.blogTitle}}</p>
            <div class="author">
              发表人：{{selectedMsg.userNickname || "匿名"}}
            </div>
            <div class="content" v-html="selectedMsg.blogContent">
            </div>

          </div>
        </div>
			</div>

			<div class="buttonDiv">
        <span class="cancle" @click="close()">取消</span>
        <span class="sure" @click="sure()">确定</span>
			</div>
		</div>
	</div>
</template>
<style lang="less" scoped>
  .selectMask{
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.4);
		z-index: 333333;
		.selectObject{
			width: 800px;
			height: 500px;
			background: white;
			margin:100px auto;
			border-radius: 8px;
			overflow: hidden;
			.selectTitle{
				font-size: 18px;
				text-align: left;
        position: relative;
        top: 1px;
			}
			.objectContent{
				display: flex;
				border-bottom: 1px solid #eee;
				height: 360px;
				>div{
					flex: 1;
				}
				.objectLeft{
					margin: 5px 0;
					>p{
						text-align: left;
  					padding-left: 50px;
  					font-size: 16px;
					}
				  .firstClass{
  					padding-left: 40px;
  					margin-top: 20px;
  					width: 200px;
            height: 290px;
            overflow: auto;
  					border-right: 1px solid #eee;
  				}	
          .firstClass::-webkit-scrollbar {/*滚动条整体样式*/
            width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
            height: 4px;
          }
          .firstClass::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
            border-radius: 5px;
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            background: rgba(0,0,0,0.2);
          }
          .firstClass::-webkit-scrollbar-track {/*滚动条里面轨道*/
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            border-radius: 0;
            background: rgba(0,0,0,0.1);
          }
				}
				.objectCenter{
					margin-top: 20px;
					.result{
						margin-top: 20px;
						padding: 0;
            height: 290px;
            overflow: auto;
						li{
							list-style: none;
							text-align: center;
							margin:14px 0 ;
						}
					}
          .result::-webkit-scrollbar {/*滚动条整体样式*/
            width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
            height: 4px;
          }
          .result::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
            border-radius: 5px;
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            background: rgba(0,0,0,0.2);
          }
          .result::-webkit-scrollbar-track {/*滚动条里面轨道*/
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            border-radius: 0;
            background: rgba(0,0,0,0.1);
          }
					.inputSearch{
						position: relative;
						input{
							border:1px solid #e9e9e9;
							height: 26px;
							border-radius: 5px;
							width:200px;
							padding: 0 36px 0 10px;
						}
						i{
						  position: absolute;
              right: 10px;
              width: 28px;
              height: 28px;
              top: 0px;
              line-height: 28px;
              cursor: pointer;
						}
					}
				}
				.objectRight{
					margin: 5px 0;
          border-left: 1px solid #eee;
          text-align: left;
					>p{
						text-align: left;
  					font-size: 16px;
            color:#727272;
            padding-left: 20px;
					}
          .content{
            text-align: left;
            height: 220px;
            overflow: auto;
            width: 200px;
            padding: 5px 5px 5px 0;
            img{
              width: 100%;
            }
          }
          .content::-webkit-scrollbar {/*滚动条整体样式*/
            width: 4px;     /*高宽分别对应横竖滚动条的尺寸*/
            height: 4px;
          }
          .content::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
            border-radius: 5px;
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            background: rgba(0,0,0,0.2);
          }
          .content::-webkit-scrollbar-track {/*滚动条里面轨道*/
            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
            border-radius: 0;
            background: rgba(0,0,0,0.1);
          }
          .objectRightContent{
            >p{
              height: 20px;
              overflow: hidden;
              width: 200px;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            padding: 0px 20px;
            .author{
              color: #939292;
              font-size: 12px;
            }
          }
					.selected{
						margin-top: 20px;
					}
				}
			}
			.buttonDiv{
				text-align: right;
				margin-top: 10px;
				margin-right: 50px;
				span{
					display: inline-block;
					width: 80px;
					height: 30px;
					border-radius: 3px;
					text-align: center;
					line-height: 30px;
					cursor: pointer;
					color: white;
					margin:20px;
				}
				.cancle{
					background: rgb(234,234,234);
				}
				.sure{
					background: rgb(76,180,231);
				}
			}
			>p{
				height: 50px;
				line-height: 50px;
				text-align: left;
				padding-left: 50px;
				background: rgb(237,237,237);
				margin:0;
				position: relative;
				.el-icon-close{
					position: absolute;
					right: 10px;
					top: 15px;
					font-size: 20px;
					color: #adacac;
					cursor: pointer;
				}
			}
		}
	}
</style>
<script>
  import {mapGetters} from 'vuex';
  import {mapState} from 'vuex';
  import category from '../../components/category'
  export default {
    data() {
      return {
        inputSearch: '',
        checked: true,
        show: false,
        radio: -1,
        detailArr: [],
        noData: true,
        selectedMsg: Object,
        pageNum: 1
      }
    },
    computed: {
      ...mapGetters({
        addMask: 'addMask',
        twoCategory: 'twoCategory'
      }),
      ...mapState({
        userInfo:(state)=>state.mutations.userInfo
      })
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    watch:{
      addMask() {
        this.selectedMsg = {};
        this.radio = -1;
        this.detailArr = [];
      },
      twoCategory(){
        this.detailArr= [];
        this.getAuditBlog(this.twoCategory.label, 1);
      }
    },
    methods: {
      handleScroll() {
        if(this.addMask.mask) {
          clearTimeout(this.timer);
          this.timer = setTimeout(()=>{
            if(this.$refs.getHeight.scrollHeight > 290 * this.pageNum){
              this.getAuditBlog(this.twoCategory, this.pageNum + 1);
            }   
          },300);
        } 
      },
    	close() {
    		this.$store.commit("ADDMASK", false);
    	},
      getAuditBlog(name, pageNum, searchParam) {
        let tagTypeName1List,tagTypeName2List;
        if(this.twoCategory.parentId == 0){
          tagTypeName1List = this.twoCategory.label
        }else{
          tagTypeName2List = this.twoCategory.label
        }
        
        let clickLocal = this.addMask.clickLocal;

         if(searchParam){
          tagTypeName1List = "";
          tagTypeName2List = "";
          clickLocal = ""
        }else{
          searchParam = "";
        }

        let param = {
          "blogTitle":searchParam,
          "tagTypeName1List": tagTypeName1List,
          "tagTypeName2List":tagTypeName2List,
          "showType": clickLocal
        }

        this.$post(`${this.url}/manage/show/blogSearch`,
          param
        )
        .then((response) => {
          if(response.code == 200){

            this.detailArr = [...this.detailArr, ...response.data.result];

            this.pageNum = response.data.pageNum;
            this.noData = this.detailArr.length === 0 ? true : false;            
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      resultSearch() {
        let result = this.inputSearch;
        this.detailArr = [];
        if(!result.trim()){
          this.$message({
            message: '搜索内容不能为空',
            type: 'warning'
          });
          return;
        }

        let param = {
          blogTitle: result 
        }

        this.getAuditBlog("", 1, result)
      },
      selectedVal(val) {
        this.selectedMsg = val;
      },
      sure() {

        let modularType2,
            achievementMsg = this.selectedMsg;

        if(!this.selectedMsg.blogId){
          this.$message({
            message: '请选择类别对象',
            type: 'warning'
          });

          return;
        }

         //isSecond == true 为成果类别
        if(this.addMask.isSecond){

          modularType2 = 2;

          this.$store.commit("ACHIEVEMENTMSG", {
            achievementMsg: achievementMsg,
            time: new Date().getTime()
          });
          this.addMask.mask = false;
          return;
        }else{
          modularType2 = 1;
        }

        let showOrder = this.addMask.showOrder;

        let clickLocal = this.addMask.clickLocal;
        let blogId = this.selectedMsg.blogId;
        let userId = this.userInfo.id;
  
        let param = {
          "modularType1": 1,
          "modularType2": modularType2,
          "blogId" : blogId,
          "showType" : clickLocal,
          "showOrder" : showOrder,
          "createUserId" : userId
        }

        this.$post(`${this.url}/manage/show/configSave`,
          param
        )
        .then((response) => {
          if(response.code == 200){
            this.addMask.mask = false;
            this.$store.commit("REFRESHDATALOCAL", {
              refreshDataLocal: clickLocal,
              isAchievementIndex: true,
              time: new Date().getTime()
            });
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      }
    },
    components: {
      category
    }
  };
</script>


