<template>
  <div class="selectImg" v-if="selectImgMask">
  	<div class="selectImgContent">
  	  <p>
  	  	选择对象
  	  	<i class="el-icon-close" @click="close()"></i>
  	  </p>
  	  <div class="selectImg_1">
  	  	<div class="selectImg_one">
  	  	  <p>1.选择展示图</p>
  	  	  <span>推荐尺寸1676*485，非必选。如未选择，则默认截取封面图中间部分作为展示图。</span>
	  	  	<el-upload
					  class="avatar-uploader"
					  :action= "fastDfsUrl"
					  :headers="myHeaders"
					  :name="name"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
          <div class="dataClear" @click="dataClear1" v-if="imgClear">
            <span></span>
          </div>
  	  	</div>

  	  	<div class="selectImg_two">
  	  		<p>2.选择成果文章</p>
  	  		<div class="selectArticle" @click="selectArticle" v-if="articleSelect">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </div>
          <div class="selectArticleShow" v-if="selectArticleShow">
            <p class="name">成果名称：{{showData.blogTitle}}</p>
            <p>发布人：{{showData.userNickname}}</p>
            <div class="dataClear" @click="dataClear">
              <span></span>
            </div>
          </div>
  	  	</div>
  	  </div>

  	  <div class="button">
  	  	<span class="cancle" @click="close">取消</span>
  	    <span class="sure" @click="sure()">确定</span>
  	  </div>
    </div>
  </div>
</template>
<style lang="less">
  .selectImg{
  	position: fixed;
  	top: 0;
  	left: 0;
  	width: 100%;
  	height: 100%;
  	z-index: 222;
  	background: rgba(0,0,0,0.4);
  	.selectImgContent{
  	  width: 600px;
  	  height: 400px;
  	  background: white;
  	  border:1px solid #eee;
  	  border-radius: 5px;
  	  position: absolute;
  	  top: 50%;
  	  left: 50%;
  	  transform: translate(-50%,-50%);  
  	  >p{
        background: rgb(237,237,237);
        margin:0;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        text-align: left;
        padding-left: 40px;
        position: relative;
        .el-icon-close{
	  	  	position: absolute;
					right: 10px;
					top: 10px;
					font-size: 20px;
					color: #adacac;
					cursor: pointer;
	  	  }
  	  }
  	  .selectImg_1{
  	  	padding-left: 40px;
  	  	text-align: left;
  	  	.selectImg_one{
  	  	  >p{
  	  	  	margin:20px 0 5px 0;
  	  	  }
  	  	  >span{
  	  	  	color: #ccc;
  	  	  	font-size: 12px;
  	  	  }
          .dataClear{
            width: 10px;
            height: 10px;
            border-radius: 100%;
            border: 1px solid red;
            position: absolute;
            left: 250px;
            top: 110px;
            line-height: 5px;
            text-align: center;
            cursor: pointer;
            span{
              height: 2px;
              background: red;
              display: inline-block;
              width: 6px;
              margin: 0 auto;
            }
          }
  	  	}
        .selectArticleShow{
          .name{
            height: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          width: 170px;
          background: #Eeee;
          padding: 1px 20px;
          font-size: 12px;
          border: 1px solid #ccc;
          position: relative;
          .dataClear{
            width: 10px;
            height: 10px;
            border-radius: 100%;
            border: 1px solid red;
            position: absolute;
            right: -18px;
            top: 0px;
            line-height: 5px;
            text-align: center;
            cursor: pointer;
            span{
              height: 2px;
              background: red;
              display: inline-block;
              width: 6px;
              margin: 0 auto;
            }
          }
        } 
        .selectArticle{
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          height: 80px;
          overflow: hidden;
          width: 80px;
        }
        .avatar-uploader{
        	margin-top: 15px;
        }
        .avatar-uploader .el-upload {
			    border: 1px dashed #d9d9d9;
			    border-radius: 6px;
			    cursor: pointer;
			    position: relative;
          height: 80px;
			    overflow: hidden;
			  }
			  .avatar-uploader .el-upload:hover {
			    border-color: #409EFF;
			  }
			  .avatar-uploader-icon {
			    font-size: 28px;
			    color: #8c939d;
			    width: 80px;
			    height: 80px;
			    line-height: 80px;
			    text-align: center;
			  }
			  .avatar {
			    width: 208px;
			    height: 208px;
			    display: block;
			  }
  	  }
  	  .button{
  	  	margin-top: 15px;
  	  	border-top: 1px solid #eee;
  	  	text-align: right;
  	  	span{
  	  		display: inline-block;
  	  		width: 80px;
  	  		height: 26px;
  	  		border-radius: 5px;
  	  		color: white;
  	  		text-align: center;
  	  		line-height: 26px;
  	  		cursor: pointer;
  	  		margin-top: 15px;
  	  	}
  	  	.cancle{
  	  		background: rgb(234,234,234);
  	  	}
  	  	.sure{
  	  		background: rgb(76,180,231);
  	  		margin:0 50px 0 30px;
  	  	}
  	  }
  	}
  }
</style>
<script>
  import {mapGetters} from 'vuex';
  import {mapState} from 'vuex';
	export default {
		data() {
			return {
				imageUrl: '',
				myHeaders: {
					requestType: "zuul"
				},
				name: "fileList",
				fastDfsUrl: this.fastDfsUrl,
        selectArticleShow: false,
        showData: Object,
        articleSelect: true,
        imgClear: false
			}
		},
    props: ['selectImgMask'],
    computed: {
      ...mapGetters({
        achievementMsg: 'achievementMsg'
      }),
      ...mapState({
        userInfo:(state)=>state.mutations.userInfo
      })
    },
    watch: {
      achievementMsg(val){
        this.getAchievementMsg(val);
      },
      selectImgMask() {
        this.dataClear();
        this.imageUrl = "";
        this.imgClear = false;
      }
    },
		methods: {
      getAchievementMsg(val) {
        this.showData = val.achievementMsg;
        this.articleSelect = false;
        this.selectArticleShow = true;
      },
      dataClear() {
        this.showData = {};
        this.articleSelect = true;
        this.selectArticleShow = false;
      },
      dataClear1() {
        this.imageUrl = "";
        this.imgClear = false;
      },
      sure() {
        let blogId = this.showData.blogId;

        if(!this.imageUrl){
          this.$message({
            message: '请上传展示图片',
            type: 'warning'
          });

          return;
        }
        
        if(this.imageUrl.indexOf("http://") == -1){
          setTimeout(()=>{
            this.save(blogId,this.imageUrl);
          })
        }else{
          this.save(blogId,this.imageUrl);
        }     
      },
      save(blogId,imageUrl) {
        let userId = this.userInfo.id;
        if(!blogId){
          this.$message({
            message: '请选择成果文章',
            type: 'warning'
          });

          return;
        }

        let param = {
          "modularType1": 1,
          "modularType2": 2,
          "blogId" : blogId,
          "showType" : 1,
          "showOrder" : 101,
          "createUserId" : userId,
          "showPictures":{
            "thumbnailsImageUrl":"",
            "pictureUrl":imageUrl,
            "videoUrl":"",
            "picWidth": 200,
            "picHight": 200,
            "type": 1
          }
        }

        this.$post(`${this.url}/manage/show/configSave`,
          param
        )
        .then((response) => {
          if(response.code == 200){
            this.$store.commit("REFRESHDATALOCAL", {
              refreshDataLocal: 1,
              time: new Date().getTime()
            });
            this.$emit("hideMask", false);
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data.fullPath;
        this.imgClear = true;
      },
      beforeAvatarUpload(file) {
        const isImg = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif';

        if (!isImg) {
          this.$message.error('上传图片只能是 jpg, png或者gif 格式');
        }
      
        return isImg;
      },
      close() {
        this.$emit("hideMask", false);
      },
      selectArticle() {
        this.$store.commit("ADDMASK", {
          mask: true,
          clickLocal: 1,
          isSecond: true
        });
      }
    }
	}
</script>


