<template>
	<div class="category">
		<el-tree
		  :data="data"
		  node-key="id"
		  :default-expanded-keys="[2, 3]"
		  :default-checked-keys="[5]"
	    @node-expand="checkItem"
      @node-click="nodeClick"
		  :props="defaultProps">
		</el-tree>
	</div>
</template>
<script>
	export default {
    data() {
      return {
        data: [{
          id: 1,
          label: '侦查技术',
          children: [
          	{
		          id: 4,
		          label: '侦查技术',
		          children: []
		        }
          ],
        },
        {
          id: 2,
          label: '安防技术',
          children: []
        }, 
        {
          id: 3,
          label: '视频图侦',
          children: []
        },
        {
          id: 4,
          label: '警用装备',
          children: []
        },{
          id: 5,
          label: '交通管理',
          children: []
        },{
          id: 6,
          label: '网络安全',
          children: []
        },
        {
          id: 7,
          label: '信息通信',
          children: []
        },
        {
          id: 8,
          label: '模拟战法',
          children: []
        },
        {
          id: 9,
          label: '无人机/机器人',
          children: []
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    created() {
      this.getOneRankCategory();
    },
    methods: {
    	close() {
    		this.$store.commit("ADDMASK", false);
    	},
      getOneRankCategory() {
        this.$post(`${this.url}/manage/category/getOneRankCategory`,
          {}
        )
        .then((response) => {
          if(response.code == 200){
            let newData = [];
            let data = response.data;
            for(var i = 0 , len = data.length ; i < len ; i++){
              newData.push({
                id: data[i].categoryId,
                label: data[i].categoryName,
                parentId: 0,
                children:[{
                  id: 4,
                  label: '',
                  children: []
                }]
              })
            }

            this.data = newData;
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
      },
      checkItem(treeData, item) {
        if(treeData.children[0].parentId){
          return;
        }

        let param = {
          parentCategoryId: treeData.id
        }
        this.$post(`${this.url}/manage/category/getTwoRankCategory`,
          param
        )
        .then((response) => {
          if(response.code == 200){
             let ajaxData = [];
             let data = response.data;
              for(var i = 0 , len = data.length ; i < len ; i++){
                ajaxData.push({
                  id: data[i].categoryId,
                  label: data[i].categoryName,
                  parentId: data[i].parentCategoryId
                })
              }

             this.createNewsTree(treeData, ajaxData);
          }else{
            this.$message.error('请求失败，请稍后重试');
          }
        })
       
      },
      createNewsTree(treeData, ajaxData) {
        this.data.forEach(item => {
          if(item.id == ajaxData[0].parentId){
            item.children = ajaxData;
          }
        })
      },
      nodeClick(data) {
        this.$store.commit("TWOCATEGORY", {
          label: data.label,
          parentId: data.parentId,
          tme: new Date().getTime()
        });
      }
    }
  };
</script>


